.wrapper {
  position: relative;
  border-radius: 12px;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  border: none !important;
  outline: none !important;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: 0.1s linear;

  &:hover {
    transform: scale(1.025);
    transition: 0.1s linear;
  }

  &.blue {
    background: linear-gradient(92.99deg, #01bffb 1.3%, #379aff 95.71%);
    box-shadow: inset -6px -4px 4px #016cdb, inset -2px 0px 21px #3e9afc,
      inset -30px -30px 60px #065ab0, inset -20px -20px 15px -12px #000000;
  }

  &.neutral {
    background: var(--neutral-button-bg);
  }

  &.pink {
    background: linear-gradient(91.79deg, #ff84ba 1.64%, #ed285f 97.77%);
    box-shadow: inset -7px -5px 5px #ff6a94, inset 10px 0px 16px #ff769c,
      inset -30px -30px 60px #ed285f, inset -20px -20px 15px -12px #000000;
  }

  &.ghost {
    background: transparent;

    &:hover {
      background: var(--neutral-button-bg);
    }
  }

  &.red {
    background: #FCA19D;
    color: white;

    &:hover {
      background: #ffbfbb;
    }
  }

  &.akbank {
    background: #b19cd7;
    border-radius: 20px;
    color: white;

    &:hover {
      background: #cdbaf1;
    }
  }

  &.landing {
    background: #438880;
    border-radius: 20px;
    color: white;

    &:hover {
      background: #5f9f98;
    }
  }

  &.member {
    background: #A6D6DA 0% 0% no-repeat padding-box;
    border: 1px solid #438880;
    color: white;
    border-radius: 26px;

    &:hover {
      background: #c1e7ea 0% 0% no-repeat padding-box;
    }
  }

  &.gray {
    background: #e6e6e6;
    border: solid 1px #e5e5e5 !important;
    color: black !important;
  }

  &.white {
    background: white;
    color: #000000;

    &:hover {
      background: rgb(235, 235, 235);
    }
  }

  &.transparentWhite {
    background: transparent;
    border: solid 1px #e5e5e5 !important;
    color: black !important;

    &:hover {
      background: rgb(241, 241, 241);
    }
  }

  &.transparentBlack {
    background: transparent;
    border: solid 1px #454545 !important;
    color: white !important;

    &:hover {
      background: rgb(17, 17, 17);
    }
  }

  &.black {
    background: black;
    color: white;

    &:hover {
      background: rgb(26, 26, 26);
    }
  }

  &.left {
    justify-content: flex-start;
  }

  &.center {
    justify-content: center;
  }

  &.right {
    justify-content: flex-end;
  }

  &.disabled {
    opacity: 0.5;
    cursor: not-allowed !important;
    pointer-events: none !important;
  }

  &.loading {
    pointer-events: none;
  }

  .fs12 {
    font-size: 12px;
  }

  .fs14 {
    font-size: 14px;
  }

  .fs16 {
    font-size: 16px;
  }

  .fs18 {
    font-size: 18px;
  }

  .fs20 {
    font-size: 20px;
  }

  .fs22 {
    font-size: 22px;
  }

  .fw400 {
    font-weight: 400;
  }

  .fw500 {
    font-weight: 500;
  }

  .fw600 {
    font-weight: 600;
  }

  .fw700 {
    font-weight: 700;
  }

  .lhNormal {
    line-height: normal;
  }
}

.text {
  display: flex;
  align-items: center;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;

  &.loading {
    opacity: 0;
    pointer-events: none;
  }

  &.blue {
    color: white;
  }

  &.pink {
    color: white;
  }

  &.neutral {
    color: var(--text);
  }
}

.fullwidth {
  width: 100%;
}

.loader {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}